import { useState } from "react";
import { Pulter } from "@pulter/react";
import "./styles.css";

export default function App() {
  const [data, setData] = useState([]);
  const [openModal, setOpenModal] = useState(false);

  const setOpen = () => {
    setOpenModal(true);
  };
  const fields = [
    {
      label: "Name",
      key: "name",
      alternateMatches: ["first name", "first"],
      fieldType: {
        type: "input"
      },
      example: "Stephanie",
      validations: [
        {
          rule: "required",
          errorMessage: "Name is required"
        }
      ]
    },
    {
      label: "Surname",
      key: "surname",
      alternateMatches: ["second name", "last name", "last"],
      fieldType: {
        type: "input"
      },
      example: "McDonald",
      validations: [
        {
          rule: "unique",
          errorMessage: "Last name must be unique",
          level: "info"
        }
      ],
      description: "Family / Last name"
    },
    {
      label: "Age",
      key: "age",
      alternateMatches: ["years"],
      fieldType: {
        type: "input"
      },
      example: "23",
      validations: [
        {
          rule: "regex",
          value: "^\\d+$",
          errorMessage: "Age must be a number",
          level: "warning"
        }
      ]
    },
    {
      label: "Full Name",
      key: "fullname",
      fieldType: {
        type: "input"
      }
    },
    {
      label: "Team",
      key: "team",
      alternateMatches: ["department"],
      fieldType: {
        type: "select",
        options: [
          { label: "Team One", value: "one" },
          { label: "Team Two", value: "two" }
        ]
      },
      example: "Team one",
      validations: [
        {
          rule: "required",
          errorMessage: "Team is required"
        }
      ]
    },
    {
      label: "Is manager",
      key: "is_manager",
      alternateMatches: ["manages"],
      fieldType: {
        type: "checkbox",
        booleanMatches: {}
      },
      example: "true"
    }
  ];

  const user = {
    id: "ojewdihucjnweud",
    name: "John Doe",
    email: "john.doe@example.com"
  };

  return (
    <div className="App">
      <input
        type="button"
        id="launch"
        value="Open Importer"
        onClick={() => setOpen()}
      />
      <div className="download">
        <a href="exampleFile.csv" target="_blank" rel="noopener noreferrer">
          Download a sample csv file here
        </a>
      </div>
      {openModal && (
        <Pulter
          templateId={"cl5ql4vw9049109kux3eqe1c3"}
          fields={fields}
          user={user}
          isOpen={openModal}
          onClose={() => {
            console.log("closed");
          }}
          rowHook={(data, addError) => {
            // Validation
            if (data.name !== "" && data.surname !== "") {
              data.fullname = data.name + " " + data.surname;
            } else {
              addError("fullname", {
                message: "First name or Last name is missing",
                level: "warning"
              });
            }
            // Transformation
            return data;
          }}
          onSubmit={(success, data) => {
            setData(data);
          }}
          allowInvalidSubmit={true}
          maxRecords={100000}
          maxFileSize={1048576}
          autoMapHeaders={true}
          autoMapDistance={2}
        />
      )}

      {data && (
        <textarea id="raw_output" readOnly value={JSON.stringify(data)} />
      )}
      {/* <h1>Hello CodeSandbox</h1>
      <h2>Start editing to see some magic happen!</h2> */}
    </div>
  );
}
